<template>
  <div class="novel">
    <div class="title">
      <span>类型：{{$route.query.type == 0 ? '微信内' : '微信外'}}</span>
      <span>ID: {{$route.query.id}}</span>
    </div>
    <Button type="success" size="large" @click="createLink" class="create">新增返回小说链接</Button>
    <Button type="primary" size="large" @click="modal = true" class="update">修改统计代码</Button>
    <div style="clear: both;"></div>
    <Table class="novel-table" :columns="novelHeader" :data="novelData"></Table>

  </div>
</template>

<script>
export default {
  data() {
    return {
      novelData: [],
      novelHeader: [
        {
          key: 'title',
          title: '文章标题',
          align: 'center'
        }, {
          title: '复制链接',
          align: 'center',
          render: (h, params) => {
            return h('Button', {
              props: {
                size: 'small',
                type: 'success'
              }
            }, '复制')
          }
        }, {
          title: 'Action',
          align: 'center',
          render: (h, params) => {
            let that = this;
            return h('div', [
              h('Button', {
                props: {
                  size: 'small',
                  type: 'primary'
                },
                on: {
                  click() {
                    that.$router.push({
                      name: '修改小说返回列表',
                      query: params.row
                    })
                  }
                }
              }, '修改'),
              h('Button', {
                props: {
                  size: 'small',
                  type: 'error'
                }
              }, '删除'),
            ])
          }
        }
      ]
    }
  },
  mounted() {
    // console.log(this.$route.query)
  },
  methods: {
    createLink() {
      this.$router.push({
        name: '创建小说返回链接',
        query: this.$route.query
      })
    }
  },
}
</script>

<style scoped>
.novel {

}
.title {
  float: left;
}

.title span {
  font-size: 24px;
  padding: 0 15px;
}
.create, .update {
  float: left;
  margin-left: 30px;
}

.novel-table {
  margin-top: 30px;
}
</style>
